import PropTypes from 'prop-types'
import React from 'react'
import {
  BlogCard,
  BlogCardExcerpt,
  BlogCardLink,
  BlogCardMeta,
  BlogCardTags,
  BlogCardTitle,
} from '../../components/BlogCard'
import Cta from '../../components/Cta'
import { Grid, GridColumn, GridContainer } from '../../components/Grid'
import IconPlus from '../../components/Icons/IconPlus'
import Slider from '../../components/Slider'
import { Title, TitleAccentLine } from '../../components/Title'
import './blog-slider.scss'

function BlogSlider({ title, titleAccent, intro, cta, posts, settings }) {
  return (
    <div className="blog-slider">
      <GridContainer>
        <Grid className="blog-slider__grid">
          <GridColumn columns="4" className="blog-slider__grid-left">
            <Title variant="small" className="blog-slider__grid-left-title">
              {title}
              <TitleAccentLine>{titleAccent}</TitleAccentLine>
            </Title>

            <div className="blog-slider__grid-left-intro">{intro}</div>

            <Cta to={cta.url} className="blog-slider__grid-left-cta">
              {cta.text}
              <IconPlus />
            </Cta>
          </GridColumn>

          <GridColumn columns="12" className="blog-slider__grid-right">
            <Slider settings={settings}>
              {posts &&
                posts.map((post) => (
                  <BlogCard key={post.id} imageUrl={post.image}>
                    <BlogCardTags tags={post.tags} />
                    <BlogCardMeta>{post.meta}</BlogCardMeta>
                    <BlogCardTitle>{post.title}</BlogCardTitle>
                    <BlogCardExcerpt>{post.excerpt}</BlogCardExcerpt>
                    <BlogCardLink to={post.url}>read more</BlogCardLink>
                  </BlogCard>
                ))}
            </Slider>
          </GridColumn>
        </Grid>
      </GridContainer>
    </div>
  )
}

BlogSlider.propTypes = {
  title: PropTypes.string.isRequired,
  titleAccent: PropTypes.string,
  intro: PropTypes.object.isRequired,
  cta: PropTypes.shape({
    url: PropTypes.string,
    text: PropTypes.string,
  }),
  posts: PropTypes.array.isRequired,
  settings: PropTypes.object,
}

export default BlogSlider
