import PropTypes from 'prop-types'
import React from 'react'
import { Grid, GridColumn, GridContainer } from '../../components/Grid'
import { Title } from '../../components/Title'
import './feature-text.scss'

function FeatureText({ children, contentAs }) {
  const TitleSlot = children.find((child) => child.type === Title)
  const ContentSlot = children.find(
    (child) => child.type === 'p' || child.type === contentAs
  )

  return (
    <GridContainer className="feature-text">
      <Grid variant="centered" className="feature-text__grid">
        <GridColumn className="feature-text__grid-column" columns="10">
          <Grid>
            {TitleSlot && <GridColumn columns="5">{TitleSlot}</GridColumn>}
            {ContentSlot && <GridColumn columns="7">{ContentSlot}</GridColumn>}
          </Grid>
        </GridColumn>
      </Grid>
    </GridContainer>
  )
}

FeatureText.propTypes = {
  children: PropTypes.array.isRequired,
  contentAs: PropTypes.func,
}

export default FeatureText
