import PropTypes from 'prop-types'
import React from 'react'
import { Parallax, ParallaxProvider } from 'react-scroll-parallax'
import Cta from '../../components/Cta'
import { Grid, GridColumn, GridContainer } from '../../components/Grid'
import IconPlus from '../../components/Icons/IconPlus'
import { BackgroundImage } from '../../components/Image'
import LocationDot from '../../components/LocationDot'
import Paper from '../../components/Paper'
import { Title, TitleAccentLine } from '../../components/Title'
import './map-callout.scss'

const mapLocations = [
  { top: '195px', left: '193px' },
  { top: '247px', left: '145px' },
  { top: '327px', left: '211px' },
  { top: '369px', left: '276px' },
  { top: '428px', left: '263px' },
  { top: '440px', left: '400px' },
  { top: '478px', left: '303px' },
  { top: '327px', left: '211px' },
  { top: '542px', left: '311px' },
  { top: '155px', left: '640px' },
  { top: '610px', left: '1274px' },
  { top: '524px', left: '386px' },
]

function MapCallout({ title, titleAccent, content, cta, image, showDots }) {
  return (
    <ParallaxProvider className="map-callout">
      <div className="map-callout__wrapper">
        <BackgroundImage className="map-callout__background" image={image}>
          {showDots &&
            mapLocations &&
            mapLocations.map((dot, i) => (
              <LocationDot key={i} top={dot.top} left={dot.left} />
            ))}
        </BackgroundImage>

        <GridContainer className="map-callout__map">
          <Grid variant="centered" className="map-callout__map-grid">
            <GridColumn columns="6">
              <Grid variant="centered">
                <Parallax y={[-20, 20]}>
                  <Paper
                    className="map-callout__map-paper"
                    variant="opaquePrimary"
                  >
                    <Grid
                      variant="centered"
                      className="map-callout__map-content"
                    >
                      <Title variant="small">
                        {title}
                        {titleAccent && (
                          <TitleAccentLine>{titleAccent}</TitleAccentLine>
                        )}
                      </Title>

                      <p>{content}</p>

                      <Cta
                        variant="white"
                        to={cta.url}
                        className="map-callout__map-button"
                      >
                        {cta.text} <IconPlus />
                      </Cta>
                    </Grid>
                  </Paper>
                </Parallax>
              </Grid>
            </GridColumn>
          </Grid>
        </GridContainer>
      </div>
    </ParallaxProvider>
  )
}

MapCallout.propTypes = {
  title: PropTypes.string.isRequired,
  titleAccent: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
  cta: PropTypes.shape({
    text: PropTypes.string,
    url: PropTypes.string.isRequired,
  }).isRequired,
  image: PropTypes.string.isRequired,
  showDots: PropTypes.bool,
}

MapCallout.defaultProps = {
  showDots: false,
}

export default MapCallout
