import PropTypes from 'prop-types'
import React from 'react'
import IconArrowRight from '../../components/Icons/IconArrowRight'
import Cta from '../Cta'
import { Title } from '../Title'
import './blog-card.scss'

const BlogCard = ({ children, imageUrl }) => {
  return (
    <div className="blog-card">
      <i
        className="blog-card__background"
        style={{ backgroundImage: `url(${imageUrl})` }}
      />
      {children}
    </div>
  )
}

BlogCard.propTypes = {
  children: PropTypes.array,
  imageUrl: PropTypes.string,
}

const BlogCardTags = ({ tags }) => (
  <div className="blog-card__tags">{tags && tags.join(', ')}</div>
)

BlogCardTags.propTypes = {
  tags: PropTypes.array.isRequired,
}

const BlogCardMeta = ({ children }) => (
  <time className="blog-card__time">{children}</time>
)

BlogCardMeta.propTypes = {
  children: PropTypes.string,
}

const BlogCardTitle = ({ children }) => (
  <Title variant="small" className="blog-card__title">
    <span>{children}</span>
  </Title>
)

BlogCardTitle.propTypes = {
  children: PropTypes.string.isRequired,
}

const BlogCardExcerpt = ({ children }) => (
  <p className="blog-card__paragraph">{children}</p>
)

BlogCardExcerpt.propTypes = {
  children: PropTypes.string.isRequired,
}

const BlogCardLink = ({ children, to }) => (
  <Cta variant="link" to={to} className="blog-card__link">
    {children}
    {/* <Image variant="cta" src={ArrowImage} alt="right arrow" /> */}
    <IconArrowRight />
  </Cta>
)

BlogCardLink.propTypes = {
  children: PropTypes.string.isRequired,
  to: PropTypes.string.isRequired,
}

export {
  BlogCard,
  BlogCardTags,
  BlogCardMeta,
  BlogCardTitle,
  BlogCardExcerpt,
  BlogCardLink,
}
