import PropTypes from 'prop-types'
import React from 'react'
import breakpoints from '../../common/breakpoints'
import { GridContainer } from '../Grid'
import Slider from '../Slider'
import { Title } from '../Title'
import './title-mixer.scss'

/**
 * TODO: to handle the opacity/color of the 1st, 2nd, 4th and 5th elements
 * we'll need to implement some of these solutions:
 * 1. Add an script to handle the opacity at initialization and slide change
 * 2. Add an overlay image with a white to transparent gradient
 *    (not recommended because it makes the effect not usable when
 *    there's no plain white background).
 */

function TitleMixer({ title, words }) {
  const verticalSlider = {
    autoplay: true,
    dots: false,
    draggable: true,
    infinite: true,
    pauseOnFocus: false,
    pauseOnHover: false,
    slidesToScroll: 1,
    slidesToShow: 5,
    swipe: false,
    swipeToSlide: true,
    vertical: true,
    verticalSwiping: true,
    responsive: [
      {
        breakpoint: breakpoints.medium,
        settings: {
          slidesToShow: 5,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: breakpoints.small,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: breakpoints.xSmall,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
        },
      },
    ],
  }

  return (
    <GridContainer className="title-mixer">
      <div className="title-mixer__left">
        <Title className="title-mixer__title">{title}</Title>
      </div>

      <div className="title-mixer__right">
        <div className="title-mixer__slider">
          <Slider settings={verticalSlider}>
            {words &&
              words.map((word) => (
                <Title
                  key={word}
                  variant="largerMediumInverted"
                  className="title-mixer__slider-title"
                >
                  {word}
                </Title>
              ))}
          </Slider>
        </div>
      </div>
    </GridContainer>
  )
}

TitleMixer.propTypes = {
  title: PropTypes.string.isRequired,
  words: PropTypes.array.isRequired,
}

export default TitleMixer
