import PropTypes from 'prop-types'
import React from 'react'
import Cta from '../../components/Cta'
import { Grid, GridColumn, GridContainer } from '../../components/Grid'
import IconPlus from '../../components/Icons/IconPlus'
import { Image } from '../../components/Image'
import Paper from '../../components/Paper'
import { Title } from '../../components/Title'
import './services.scss'

function Services({ services }) {
  return (
    <GridContainer>
      <Grid variant="centered">
        <GridColumn columns="10">
          <Paper variant="noPadding" className="services__paper">
            {services &&
              services.map((item) => (
                <div key={item.title} className="services__box">
                  <div className="services__wrapper">
                    <Image src={item.icon} variant="icon" />
                    <Title variant="cardTitle">
                      {item.title} <br />
                      <span>{item.subtitle}</span>
                    </Title>

                    <div className="services__content">{item.content}</div>

                    <Cta to={item.cta.url} className="services__cta">
                      {item.cta.text}
                      <IconPlus />
                    </Cta>
                  </div>
                </div>
              ))}
          </Paper>
        </GridColumn>
      </Grid>
    </GridContainer>
  )
}

Services.propTypes = {
  services: PropTypes.array.isRequired,
}

export default Services
