import PropTypes from 'prop-types'
import React from 'react'
import './stat.scss'

function Stat({ children }) {
  return <div className="stat">{children}</div>
}

Stat.propTypes = {
  children: PropTypes.node.isRequired,
}

function StatNumber({ children }) {
  return <div className="stat__number">{children}</div>
}

StatNumber.propTypes = {
  children: PropTypes.node.isRequired,
}

function StatTitle({ children }) {
  return <div className="stat__title">{children}</div>
}

StatTitle.propTypes = {
  children: PropTypes.node.isRequired,
}

export { Stat, StatNumber, StatTitle }
